.c-btn {
  display: block;
  text-align: center;
  padding: 1.625rem;
  background: #fff;
  color: #3F3F3F;
  letter-spacing: 0.15em;
  text-transform: uppercase;
  border: 1px solid #D9D9D9;
  width: 100%;
  max-width: 360px;
