  margin-right: auto;
  font-size: 1rem;
}
@media screen and (max-width: 767px) {
  .c-btn {
    padding: 1.3125rem;
    max-width: 328px;
  }
}

.c-btn span {
  position: relative;
  margin-left: -3.125rem;
}

.c-btn span::before,
.c-btn span::after {
  content: "";
  position: absolute;
}

.c-btn span::before {
  top: 66%;
  left: calc(88% + 25px);
  transform: translateY(-50%);
  width: 40px;
  height: 1px;
  background-color: #3F3F3F;
}

.c-btn span::after {
  top: calc(68% - 2px);
  transform: translateY(calc(-50% - 2px)) rotate(45deg);
  width: 10px;
  height: 1px;
  right: -3.25rem;
  background: #3F3F3F;
}

@media screen and (min-width: 768px) {
  .c-btn:hover {
    background: #eee;
    transition: background 0.5s ease;
  }
}

.c-btn--contact span::before {
  left: calc(78% + 25px);
}

.c-btn--contact span::after {
  right: -3.125rem;
}

